.labelTitle {
	margin-bottom: 14px;
	font-family: "Poppins", sans-serif;
	font-weight: 400;
	font-size: 14px;
	letter-spacing: 0.02em;
	color: var(--color-text-base);
}

.radioGroup {
	display: flex;
	flex-direction: column;
}

.radioLabel {
	display: flex;
	align-items: center;

	position: relative;
	cursor: pointer;
	font-size: 14px;
	line-height: 1.5;
}

.radioLabel:not(:last-child) {
	margin-bottom: 12px;
}

.radioInput {
	display: none;
}

.labelText {
	font-weight: 400;
	font-size: 12px;
	letter-spacing: -0.02em;
	color: var(--card-text);
	margin-left: 10px;
}

.customRadio {
	width: 14px;
	height: 14px;
	border-radius: 50%;
	background: currentColor;
	position: relative;
	transition: all 0.2s ease;
}

.customRadio::before {
	content: '';
	display: block;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: transparent;
	border: 2px solid transparent;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: all 0.2s ease;
}

.radioInput:checked+.customRadio {
	border: 1px solid currentColor;
	background: #1e1e1e;
}

.radioInput:checked+.customRadio::before {
	background: currentColor;
	border-color: currentColor;
}


.blueLabel {
	color: #6C89FA;
}

.pinkLabel {
	color: #F99FB4;
}

.greenLabel {
	color: #B4E1A8;
}

.blackLabel {
	color: #555555;
}